const sexArray = ['保密', '男', '女']
const emotionArray = ['保密', '未婚', '已婚']
import uniListItem from '@/components/uni-ui/uni-list-item/uni-list-item.vue';

import mpvueCityPicker from '@/components/uni-ui/mpvue-citypicker/mpvueCityPicker.vue';

import { mapState } from 'vuex'

export default {
	components: {
		uniListItem,
		mpvueCityPicker
	},
	data() {
		return {
			themeColor: '#007AFF',
			cityPickerValueDefault: [0, 0, 1],
			pickerText: '',
			
			username:"昵称",
			sex:0,
			emotion:0,
			job:"保密",
			birthday:"2019-03-18"
		}
	},
	// 监听返回
	onBackPress() {
	  if (this.$refs.mpvueCityPicker.showPicker) {
	  	this.$refs.mpvueCityPicker.pickerCancel();
	    return true;
	  }
	},
	// 监听页面卸载
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	},
	onLoad() {
		let userinfo = this.user.userinfo
		if(userinfo){
			this.pickerText = userinfo.path
			this.username = this.user.username
			this.sex =  userinfo.sex
			this.emotion = userinfo.qg
			this.job  = userinfo.job
			this.birthday  = userinfo.birthday
		}
	},
	computed: {
		...mapState({
			user:state=>state.user
		}),
		sexText() {
			return sexArray[this.sex]
		},
		emotionText(){
			return emotionArray[this.emotion]
		}
	},
	methods: {
		showCityPicker(){
			this.$refs.mpvueCityPicker.show()
		},
		// 三级联动提交事件
		onConfirm(e) {
			this.pickerText = e.label
		},
		// 修改生日
		onDateChange(e){
			this.birthday = e.detail.value
		},
		// 修改头像
		changeUserpic(){
			uni.chooseImage({
				count:1,
				sizeType:["compressed"],
				sourceType:["album","camera"],
				success: (res) => {
					this.$H.upload('/edituserpic',{
						filePath: res.tempFilePaths[0],
						name: 'userpic',
						token:true
					}).then(result=>{
						console.log(result);
						this.$store.commit('editUserInfo',{
							key:"userpic",
							value:result.data
						})
						uni.showToast({
							title: '修改头像成功',
							icon: 'none'
						});
					}).catch(err=>{
						console.log(err);
					})
				}
			})
		},
		// 修改性别
		changeSex(){
			uni.showActionSheet({
			    itemList: sexArray,
			    success:(res)=>{
			        this.sex = res.tapIndex
			    }
			});
		},
		// 修改情感
		changeEmotion(){
			uni.showActionSheet({
			    itemList: emotionArray,
			    success:(res)=>{
			        this.emotion = res.tapIndex
			    }
			});
		},
		// 修改职业
		changeJob(){
			let JobArray = ['IT','教师','农名']
			uni.showActionSheet({
			    itemList: JobArray,
			    success:(res)=>{
			        this.job = JobArray[res.tapIndex]
			    }
			});
		},
		// 提交
		submit(){
			let obj = {
				name:this.username,
				sex:this.sex,
				qg:this.emotion,
				job:this.job,
				birthday:this.birthday,
				path:this.pickerText
			}
			this.$H.post('/edituserinfo',obj,{
				token:true
			}).then(res=>{
				console.log(res);
				this.$store.commit('editUserInfo',{
					key:"username",
					value:this.username
				})
				this.$store.commit('editUserUserInfo',obj)
				uni.navigateBack({
					delta: 1
				});
				uni.showToast({
					title: '修改资料成功',
					icon: 'none'
				});
			})
		}
	}
}
